.container {
  padding: 0 32px;
  height: 100%;
  user-select: none;
  .card {
    height: 100%;
    width: 100%;
    border-radius: 12px;
    padding: 8px;
    position: relative;
    .descOne {
      position: absolute;
      width: 40px;
      top: 8px;
      right: 8px;
    }
    .descTwo {
      position: absolute;
      bottom: 8px;
      left: 8px;
      right: 8px;
    }
    .descThree {
      position: absolute;
      top: 8px;
      left: 8px;
      right: 56px;
      bottom: 80px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .name {
      font-family: 'lixunkejingdianxingshu';
      font-weight: normal;
      font-size: 70px;
      line-height: 72px;
      writing-mode: vertical-lr;
    }
    .cmyk {
      .progress {
        padding-top: 8px;
        padding-bottom: 8px;
        position: relative;
        sub {
          position: absolute;
          top: 0;
          left: 0;
          font-size: 10px;
        }
      }
    }
    .rgb {
      .progress {
        display: flex;
        align-items: center;
        height: 14px;
        .label {
          margin-right: 4px;
          font-size: 12px;
        }
      }
    }
    .info {
      margin-bottom: 4px;
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      text-transform: uppercase;
    }
    :global {
      .ant-progress {
        color: currentColor !important;
        margin: 0 !important;
      }
      .ant-progress-text {
        color: currentColor !important;
      }
    }
  }
}
