.container {
  height: 100%;
  user-select: none;
  display: flex;
  flex-direction: column;
  .header {
    height: 60px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);
    padding: 0 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .content {
    flex: 1;
  }
  .list {
    display: flex;
    flex-wrap: wrap;
    padding: 32px;
    .wrap {
      width: 12.5%;
      padding: 1px;
    }
    .item {
      cursor: pointer;
      transition: all 0.2s;
      font-size: 14px;
      line-height: 16px;
      text-align: center;
      height: 86px;
      position: relative;
      border-radius: 3px;
      .title, .value {
        position: absolute;
        display: block;
        left: 0;
        width: 100%;
        transition: all 0.2s;
      }
      .title {
        top: 50%;
        transform: translateY(-50%);
      }
      .value {
        bottom: 0;
        opacity: 0;
      }
      &:hover {
        .title {
          top: 8px;
          transform: translateY(0%);
        }
        .value {
          bottom: 8px;
          opacity: 0.7;
        }
      }
    }
  }
}
